import React from 'react';
import '../App.css';
import {Summary} from './summary';

class Checkout extends React.Component{

    state = {
        mess: ''
    }

    setRequest = (e) => {
        e.preventDefault()

        // Проверяем поля формы
        if(
            this.state.username === undefined
            || this.state.useremail === undefined
            || this.state.userphone === undefined){
                this.setState({mess: 'Заполните обязательные поля'});
                return;
        }

        let json = createRequestData(this.props.data, this.state);
        let svgs = this.props.svgs;
        let url = this.props.data.importOptions.REQUEST_URL;
        let formData = new FormData();
        formData.append('order', json);
        formData.append('svg', JSON.stringify(svgs));
        formData.append('mailto', this.props.data.importOptions.MAILTO);
        this.setState(
            {mess: 'Отправка заявки...'},
            () => fetch(url, { method: 'POST', body: formData })
                .then(
                    (res) => {
                        this.setState({mess: 'Заказ создан успешно.'});
                    },
                    (error) => {
                        this.setState({mess: 'Ошибка при создании заказа #' + error});
                    }
                )
        )
        
    }

    heandleChange = (e) => {
        let target = e.target;
        let fieldName = target.name;
        const value = target.type === 'checkbox' ? target.checked : target.value;

        this.setState(
            {[fieldName]: value, mess: ''},
            () => {
                if(fieldName === 'faster' || fieldName === 'inbox' || fieldName === 'poddon')
                    this.props.setOrderDataFunc(fieldName, this.state[fieldName]);
            }
        );        
    }

    render(){
        const data = this.props.data;
        const billing = data.billingOptions;
        let message = this.state.mess ? <div id="message">{this.state.mess}</div> : '';
        return(
            <>
            <Summary set={data} options={data.importOptions} />
            <h4 className="tac">Заполните форму ниже</h4>
            <form onSubmit={this.setRequest} id="checkoutForm">
                <fieldset className="flex w100p">
                    <legend>Ваши данные</legend>
                    <label className="reqiured-field">
                        <input type="text" name="username" required defaultValue={this.state.username} placeholder="ФИО" onChange={this.heandleChange} />
                    </label>
                    <label className="reqiured-field">
                        <input type="phone" name="userphone" required defaultValue={this.state.userphone} placeholder="Телефон" onChange={this.heandleChange} />
                    </label>
                    <label className="reqiured-field">
                        <input type="email" name="useremail" required defaultValue={this.state.useremail} placeholder="Ваш email" onChange={this.heandleChange} />
                    </label>
                    <label className="">
                        <textarea name="usercomment" defaultValue={this.state.usercomment} placeholder="Комментарий к заказу" onChange={this.heandleChange}></textarea>
                    </label>
                </fieldset>
                <fieldset className="flex w100p">
                    <legend>Дополнительно</legend>
                    <label className="flex-center">
                        <span className="fons12">Срочный распил</span>
                        <input type="checkbox" name="faster" checked={billing.faster} onChange={this.heandleChange} />
                    </label>
                    <label className="flex-center">
                        <span className="fons12">Упаковка в плёнку</span>
                        <input type="checkbox" name="inbox" checked={billing.inbox} onChange={this.heandleChange} />
                    </label>
                    <label className="flex-center">
                        <span className="fons12">Упаковка на поддон</span>
                        <input type="checkbox" name="poddon" checked={billing.poddon} onChange={this.heandleChange} />
                    </label>
                </fieldset>
                <fieldset className="w100p">
                    <legend>Доставка</legend>
                    <label>
                        <input type="text" name="billingaddress" defaultValue={this.state.billingaddress} placeholder="Адрес доставки" onChange={this.heandleChange} />
                    </label>
                </fieldset>
            </form>
            <div id="calc-form-controlls-wrapper">
                <button className="btn" onClick={this.props.toOrderFunc}>Назад к списку</button>
                <button className="btn" onClick={this.setRequest}>Создать заказ</button>
            </div>
            {message}
            </>
        );
    }
}

export {Checkout};

function createRequestData(data,form){
    let details = data.details;
    let o = data.importOptions;
    let date = new Date();
    let userData = `${form.username} ${form.userphone} ${form.useremail}`;
    let space = '';
    let order = [
        [space],
        ['Заказчик',userData,space,space,space,'дата',formatDate(date)],
        [space],
        ['Цвет плиты',o.SHEET_COLOR,space,'Толщина плиты',o.SHEET_DEPTH],
        [space],
        ['Цвет кромки',space,'Обозначение',space],
        ['Цвет кромки',space,'Обозначение',space],
        ['Цвет кромки',space,'Обозначение',space],
        [space],
        ['ПОЗИЦИЯ','Длина','Ширина','Кол-во','Крома L1','Крома L2','Крома W1','Крома W2','Паз','Комментарий'],
        [space]
    ]
    //console.log(order);
    let ind = 1;
    details.forEach((d,key) => {
        let textureRotate = d.textureRotate;
        let width = d.nominalwidth;
        let length = d.nominalheight;
        let edgeL1 = d.edge.left;
        let edgeL2 = d.edge.right;
        let edgeW1 = d.edge.bot;
        let edgeW2 = d.edge.top;
        // Если текстура повёрнута, меняем значения ширина-длина
        if(textureRotate === 0){
            width = d.nominalheight;
            length = d.nominalwidth;
        }
        // Если текстура повёрнута, меняем значения кромок L-W
        if(textureRotate === 0){
            edgeL1 = d.edge.bot;
            edgeL2 = d.edge.top;
            edgeW1 = d.edge.left;
            edgeW2 = d.edge.right;
        }
        // Добавляем значения в таблицу
        let row = [
            `#${ind} ${key}`,
            length,
            width,
            d.count,
            edgeL1,edgeL2,edgeW1,edgeW2,
            _e(d.groove),
            createComment(d,form)
        ];
        order.push(row);
        ind++;
    })
    return JSON.stringify(order);
}

function createComment(d,form){
    let comment = '';
    let breakString = '\n';
    // Добавляем углы
    if(d.lt.x > 0 && d.lt.y > 0)
        comment += `Левый верхний: ${_e(d.lt.type)} | X:${d.lt.nomX} Y:${d.lt.nomY}` + breakString;
    if(d.rt.x > 0 && d.rt.y > 0)
        comment += `Правый верхний: ${_e(d.rt.type)} | X:${d.rt.nomX} Y:${d.rt.nomY}` + breakString;
    if(d.rb.x > 0 && d.rb.y > 0)
        comment += `Правый нижний: ${_e(d.rb.type)} | X:${d.rb.nomX} Y:${d.rb.nomY}` + breakString;
    if(d.lb.x > 0 && d.lb.y > 0)
        comment += `Левый нижний: ${_e(d.lb.type)} | X:${d.lb.nomX} Y:${d.lb.nomY}` + breakString;
    // Добавляем петли
    if(d.loops && d.loops.side !== 'none'){
        comment += `Кол-во петель: ${d.loops.count}` + breakString;
        comment += `Сторона: ${_e(d.loops.side)}` + breakString;
        comment += `До первой: ${d.loops.before}` + breakString;
        comment += `До последней: ${d.loops.after}` + breakString;
    }
    // Добавляем доп.опции
    if(form.faster)
        comment += 'Срочный распил - да' + breakString;;
    if(form.inbox)
        comment += 'Упаковка в плёнку - да' + breakString;;
    if(form.poddon)
        comment += 'Упаковка в поддон - да' + breakString;;

    return comment;
}

function formatDate(date) {
    var dd = date.getDate();
    if (dd < 10) dd = '0' + dd;
  
    var mm = date.getMonth() + 1;
    if (mm < 10) mm = '0' + mm;
  
    var yy = date.getFullYear() % 100;
    if (yy < 10) yy = '0' + yy;
  
    return dd + '.' + mm + '.' + yy;
}

function _e(text){
    // Sides
    if(text === 'left')
        return 'слева';
    else if(text === 'top')
        return 'сверху';
    else if(text === 'right')
        return 'справа';
    else if(text === 'bot')
        return 'снизу';
    // Angles
    else if(text === 'outside')
        return 'Наружный';
    else if(text === 'linear')
        return 'Прямой';
    else if(text === 'inside')
        return 'внутренний';
    // Default
    else
        return text;
}