// Helpful functions

class Helper{

    prepareNumber(number){
        return number;
    }
    scalingW(number,set){
        return number/(set.width/set.nominalwidth);
    }
    scalingH(number,set){
        return number/(set.height/set.nominalheight);
    }
    enscalingW(number,set){
        return number/(set.nominalwidth/set.width);
    }
    enscalingH(number,set){
        return number/(set.nominalheight/set.height);
    }
    enscalingAny(number,set,side){
        let os = side === 'left' || side === 'right' ? 'height' : 'width';
        if(set.nominalwidth >= 500){
            return number/(set['nominal'+os]/(set[os]));
        }
        else
            return number;
    }
    descalingAny(number,set,side){
        let os = side === 'left' || side === 'right' ? 'height' : 'width';
        if(set.nominalwidth >= 500){
            return Math.trunc((number-70)*(set['nominal'+os]/(set[os])));
        }
        else
            return number;
    }
    descalingAnyV2(number,set,side){
        let os = side === 'left' || side === 'right' ? 'height' : 'width';
        if(set.nominalwidth >= 500){
            return Math.trunc(number*(set['nominal'+os]/(set[os])));
        }
        else
            return number;
    }
}

export {Helper};